$action-seq-list-icon-border: 3px;
.ma__action-seq-list {
  @include ma-component-spacing;

  &__items {
    @include ma-reset-list;
    counter-reset: li; // Initiate a counter
  }

  &__item {
    list-style:none; // Disable the normal item numbering
    margin-bottom: 28px;
    position: relative;

    @media ($bp-small-min) {
      padding-left: 60px;
    }


    &:before {
      border: $action-seq-list-icon-border solid;
      border-radius: 50%;
      content: counter(li); // Use the counter as content
      counter-increment: li; // Increment the counter by 1
      font-size: 1.25rem;
      height: 36px;
      line-height: 36px - $action-seq-list-icon-border * 2;
      position: absolute;
        top: 0;
        left: 0;
      text-align: center;
      width: 36px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__title {
    min-height: 32px;
    padding-left: 50px;

    @media ($bp-small-min) {
      margin-bottom: 0;
      padding-left: 0;
    }
  }

  .ma__rich-text {
    padding-left:0;
    padding-right:0;
  }
}
